export default {
	data() {
		return {
			order: null,
			act: null
		};
	},
	onLoad(opt) {
		if (opt.id) {
			this.$Fetch({
				url: 'activity_list/getDataById',
				data: { id: opt.id },
				success: res => {
					console.log(res);
					this.order = res;
					this.act = res.activity;
				}
			});
		}
	}
};
